body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:#222;
}

button{
  border-radius: 0.5em;
  padding:10px;
  width:200px;
  font-weight:bold;
  font-size:15px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


/*=======================================================
// Mobile Style
//=======================================================*/



/*main class container*/
.Container
{
    background-color:#eee;
    margin:0;
    height:100vh;
}


.link-button
{
    display:inline-block;
    border:none;
    padding:0;
    margin:0;
    width: auto;
    background:none;
    color:dodgerblue;
    cursor: pointer;
}

.link-button:hover{
    text-decoration:underline;
}

/*=======================================================
// Desktop Style
//=======================================================*/

@media only screen and (min-width: 768px) {
    .Container
    {
        margin:0 auto;
        margin-top:60px;
        width:650px;
        height:800px;
        border:1px solid black;
        border-radius:1em;
        overflow:hidden;
    }

  }
header
{
    background-color:dodgerblue;
    height:15%;
    text-align:center;
    border-bottom:1px solid black;
}


h1{
    margin:0;
    padding-top:7%;
    text-align:center;
    display:inline-block;
    color:white;
}

.nav-button
{
    position:absolute;
    color:black;
    padding:0;
    width:60px;
    height:30px;
    top:5%;
    right:5%;
    /* display:inline; */
    border: 1px solid black;
    border-radius:0.2em;
    font-size:1em;
    background-color:white;
}

.nav-button:hover{
    background-color:lightgrey;
}

/*=======================================================
// Desktop Style
//=======================================================*/

@media only screen and (min-width: 768px) {
    
    /* Fix our nav button for full-screen apps */
    .nav-button{
        position:relative;
        top:-35px;
        right:-250px;
    }

    /* Display block looks much better on desktop */
    h1{
        display: block;
    }

  }
main
{
    height:77%;
    overflow-y:scroll;
    overflow-x:hidden;
    position:relative;
}

.deck-link
{
    width:100%;
    color:black;
    height:80px;
    background-color:#eee;
    border:none;
    border-bottom:1px solid #777;
    font-weight:bold;
    font-size:1em;
    border-radius:0px;
}

.deck-stats
{
    border-bottom:1px solid #777;
    text-align:center;
    padding-bottom:15px;
}

.deck-stats ul{
    text-align:left;
}

.deck-stats li{
    list-style:none;
    font-size:0.8em;
}

.deck-button-start
{
    background-color:green;
    border:1px solid darkgreen;
    border-radius: 0.5em;
    padding:10px;
    width:200px;
    font-weight:bold;
    color:white;
    font-size:15px;
}

.deck-button-start:hover{
    background-color:rgb(13, 151, 54);

}
.deck-error
{
    margin-top:20%;
    text-align:center;
    color:red;
}

.deck-error p{
    font-weight:bold;
}

.deck-error-button
{
    margin-top:30px;
    background:none;
    border:none;
    font-size:60px;
}

.deck-error-button:hover
{
    font-size:65px;
}
.card-front
{
    text-align:center;
}

.card-front h2
{
    margin-top:190px;
    font-size: 3em;
}

.cardfront-button-flip
{
    margin-top:80px;

    background-color:dodgerblue;
    border:1px solid darkblue;
}

.cardfront-button-flip:hover{
    background-color:rgb(82, 169, 255);
}
.card-back
{
    padding-left:20px;
    padding-right:20px;
}

.card-back h2
{
    text-align:center;
    font-size:2em;
    margin-bottom:50px;
}

.cardback-pos{
    margin-top:2px;
    text-align:center;
    font-size:1.2em;
}

.cardback-usage
{
    font-style:italic;
    padding-left:20px;
    padding-right:8px;
    font-size:0.9em;
    color:#555;
}

.card-back button{
    margin:10px;
    width:120px;
}

.cardback-button-right
{
    background-color:rgb(24, 175, 31);
    border:1px solid darkgreen;
}

.cardback-button-right:hover{
    background-color:rgb(28, 184, 74);
}


.cardback-button-wrong
{
    background-color:rgb(255, 30, 30);
    border:1px solid darkred;
}

.cardback-button-wrong:hover{
    background-color:rgb(252, 61, 61);;

}

.cardback-button-container
{
    margin-top:20%;
    text-align:center;
}

@media only screen and (min-width: 768px) {
    
    .card-back
    {
        padding-left:50px;
        padding-right:50px;
    }
  }
.results-container
{
    padding-left:20px;
    padding-right:20px;
    text-align:center;
}

.results-container h2
{
    font-size:2em;
    margin-bottom:50px;
}

.results-container button{
    margin:10px;
    width:120px;
    color:black;
}

.results-container p{
    font-size:1.2em;
}

.results-button-again
{
    background-color:dodgerblue;
    border:1px solid darkblue;
}

.results-button-again:hover{
    background-color:rgb(82, 169, 255);
}

.results-button-home
{
    background-color:rgb(24, 175, 31);
    border:1px solid darkgreen;
}

.results-button-home:hover{
    background-color:rgb(13, 151, 54);
}

.results-button-container
{
    margin-top:20%;
    text-align:center;
}

.results-error
{
    margin-top:30px;
    text-align:center;
    color:red;
}

.results-error p{
    font-weight:bold;
    font-size:14px;
}

.results-error-button
{
    margin-top:30px;
    background:none;
    border:none;
    font-size:30px;
}

.results-error-button:hover
{
    font-size:35px;
}

@media only screen and (min-width: 768px) {
    
    .results-container
    {
        padding-left:50px;
        padding-right:50px;
    }
}

.login-form{
    margin-top:30px;
    padding-left:15%;
    padding-right:15%;
}

.login-form label{
    display:block;
    text-align:left;
    font-weight:bold;
}

.login-form input{
    width:100%;
    height:30px;
    margin-top:10px;
    margin-bottom:10px;
    border:1px solid black;
    border-radius:0.2em;
    font-size:18px;
}

.login-form p{
    text-align:center;
}

.login-button{
    background-color:dodgerblue;
    border:1px solid darkblue;
    width:100%;
    margin-top:20px;
}

.login-button:hover{
    background-color:rgb(82, 169, 255);
}

.login-form span{
    color:red;
}
.loading
{
    background-color:rgba(0, 0, 0, 0.6);
    height:100%;
    width:100%;
    position:absolute;
    top:0;
    left:0;
    z-index:2;
}

.loading-spinner
{
    margin:0 auto;
    margin-top:40%;
    height:50px;
    width:50px;
    color:white;
}
.register-form{
    margin-top:30px;
    padding-left:15%;
    padding-right:15%;
}

.register-form label{
    display:block;
    text-align:left;
    font-weight:bold;
}

.register-form input{
    width:100%;
    height:30px;
    margin-top:10px;
    margin-bottom:10px;
    border:1px solid black;
    border-radius:0.2em;
    font-size:18px;
}

.register-form p{
    text-align:center;
}

.register-form span{
    color:red;
}
.info
{
    background-color:rgba(255, 255, 255, 1);
    position:absolute;
    background-color:rgb(192, 233, 250);
    top:0;
    left:0;
    height:100%;
    z-index:3;

    width:100%;
}

.info-container
{
    padding-left:20px;
    padding-right:20px;
    text-align:center;
}

.info-container h2
{
    margin-top:20px;
}

.info-container p{
    text-align:left;
}

.info-container button{
    margin-top:30px;
    border:1px solid black;
}

.info-container button:hover{
    background-color:#eee;
}

footer{
    background-color:rgb(130, 6, 155);
    color:white;
    text-align:center;
    height:8%;
    padding-top:4%;
}

footer .link-button{
    color:lime;
}
